import slideDown from './slide-down';
import slideUp from './slide-up';
import { ATTR_NAME, STATES, DEF_TIME } from './const';

export default (sel, timeMs = DEF_TIME) => {
  const slideEl = document.querySelector(sel);
  let state = slideEl.getAttribute(ATTR_NAME);
  if (!state) {
    state = slideEl.offsetHeight === 0 ? STATES.ClOSE : STATES.OPEN;
  }
  switch (state) {
    case STATES.ClOSE:
      return slideDown(sel, timeMs);
      break;
    case STATES.OPEN:
      return slideUp(sel, timeMs);
      break;
  }
};
