import { ATTR_NAME, STATES, DEF_TIME, DEF_TIME_EXEC } from './const';

export default (sel, timeMs = DEF_TIME) => {
  return new Promise(
    (resolve, reject) => {
      try {
        const slideEl = document.querySelector(sel);
        slideEl.style.overflow = 'hidden';
        let hEl = slideEl.offsetHeight;
        const step = hEl * DEF_TIME_EXEC / timeMs;
        const timer = setInterval(
          () => {
            hEl -= step;
            slideEl.style.height = `${hEl}px`;
            if (hEl < 0) {
              requestAnimationFrame(() => {
                slideEl.style.height = '0px';
                slideEl.setAttribute(ATTR_NAME, STATES.ClOSE);
                resolve();
              });
              clearInterval(timer);
            }
          }, DEF_TIME_EXEC
        );
      } catch (err) {
        reject(err);
      }
    }
  );
};
