import { ATTR_NAME, STATES, DEF_TIME, DEF_TIME_EXEC } from './const';

export default (sel, timeMs = DEF_TIME) => {
  return new Promise(
    (resolve, reject) => {
      try {
        const slideEl = document.querySelector(sel);
        const hEl = slideEl.scrollHeight;
        let currH = 0;
        const step = hEl * DEF_TIME_EXEC / timeMs;
        const timer = setInterval(
          () => {
            currH += step;
            slideEl.style.height = `${currH}px`;
            if (currH > hEl) {
              requestAnimationFrame(() => {
                slideEl.style.height = '';
                slideEl.style.overflow = 'auto';
                slideEl.setAttribute(ATTR_NAME, STATES.OPEN);
                resolve();
              });
              clearInterval(timer);
            }
          }, DEF_TIME_EXEC
        );
      } catch (err) {
        reject(err);
      }
    }
  );
};
