import { ATTR_NAME, STATES, DEF_TIME } from './const';
import { init } from './events';
import slideDown from './slide-down';
import slideUp from './slide-up';

export default (sel, timeMs = DEF_TIME)  => {
  const slideEl = document.querySelector(sel);
  if (slideEl.offsetHeight === 0) {
    slideEl.style.height = '0px';
    slideEl.setAttribute(ATTR_NAME, STATES.ClOSE);
  } else {
    slideEl.setAttribute(ATTR_NAME, STATES.OPEN);
  }

  init(sel, newState => {
    switch (newState) {
      case STATES.ClOSE:
        return slideUp(sel, timeMs);
        break;
      case STATES.OPEN:
        return slideDown(sel, timeMs);
        break;
    }
  });
};
