import { ATTR_NAME } from './const';
const MutationObserver = window.MutationObserver || window.WebKitMutationObserver || window.MozMutationObserver;
const config = { 
  attributes: true,
  attributeOldValue: true, 
  childList: false, 
  subtree: false 
};
const lsInitEls = [];

function handler(mutationsList) {
  for(let mutation of mutationsList) {
    if (mutation.attributeName === ATTR_NAME) {
      const newValue = mutation.target.getAttribute(ATTR_NAME);
      if (mutation.oldValue !== newValue) {
        lsInitEls.forEach(
          el => {
            el.callback(newValue);
          }
        );
      }
    }
  }
};

export function init(sel, callback) {
  const el = document.querySelector(sel);
  let observer = new MutationObserver(handler);
  observer.observe(el, config);
  lsInitEls.push({
    sel,
    observer,
    callback
  });
}

export function dispose(sel) {
  const ind = lsInitEls.findIndex(
    el => el.sel === sel
  );
  const el = lsInitEls.splice(ind, 1)[0];
  el.observer.disconnect();
}
